@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.TrainRecord {
  width: 380px;
  margin: 50px auto;
}

.TrainRecordForm {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.TrainRecordForm label {
  display: flex;
  flex-direction: column;
}

.TrainRecordForm__date, .TrainRecordForm__dist {
  width: 140px;
  height: 45px;
  border-radius: 5px;
  padding: 10px 15px;
  margin: 10px 10px 0px 0px;
  font-size: 16px;
  box-sizing: border-box;
}

.TrainRecordForm__button {
  width: 45px;
  height: 45px;
  border-radius: 5px;
}

.TrainRecord__history {
  margin-top: 20px;
}

.TrainRecord__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.TrainRecord__table {
  margin: 0px;
  padding: 20px;
  border: 2px solid rgb(0, 34, 73);
  border-radius: 5px;
  list-style: none;
}

.TrainRecord__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.TrainRecord__item i {
  font-size: 18px;
  margin-left: 5px;
  cursor: pointer;
}
